import React, {useEffect, useImperativeHandle, useState} from "react";
import {withRouter} from "react-router-dom";

import {
    Button,
    Checkbox,
    Form,
    Input, InputNumber, message,
    Select,

} from "antd";

const {TextArea} = Input;
import styles from './tcp.module.less';
import {io} from "socket.io-client";
import {arrayBuffer2Hex, arrayBuffer2String, buffer_to_hex, getDateTime, toArrayBuffer, toBuffer} from "@/utils/utils";
import { wsUrl } from "@/services/request";

let listMessage = []
var socketClient = null;
let receiveType = "1"
const TcpType = (props: any) => {
    const {dispatch, select, linkDetail} = props;
    const [form] = Form.useForm();

    const [linksSwitch, setLinksSwitch] = useState<boolean>(false);

    const [startTime, setStartTime] = useState([]);
    const [endTime, setEndTime] = useState([]);
    const [list, setList] = useState([]);

    const [formData, setFormData] = useState<any>({
        sendingType: "1",
        receiveType: "1",
        cycleState: false,
        timeMS: "",
    });


    //useImperativeHandleref
    useImperativeHandle(props.onRef, () => {
        return {
            "getLinkStatus": () => {
                if (linksSwitch) {
                    closeLinks()
                    return false
                } else {
                    getLinksInfo()
                    return true
                }

            }

        };
    });

    const getLinksInfo = () => {


        socketClient = new WebSocket(wsUrl);
        socketClient.onopen = (e) => {
            socketClient.send(JSON.stringify({
                event: 'open_tcp',
                data: {ip: linkDetail.ipAddress, port: linkDetail.portNumber}
            }))

            setLinksSwitch(true)

            const time = getDateTime();
            setStartTime(time)
        };
        socketClient.onmessage = (e) => {
            const res = e.data ? JSON.parse(e.data) : e.data;

            console.log(formData)
            let value = ""
            if (receiveType === "1") { // ASCII
                console.log('ASCII '+receiveType, res);

                // let buffer = Buffer.from(res.data);
                // console.log('ASCII buffer', buffer.toString());

                if (res.data.length > 0) {
                    const arrayBuffer = toArrayBuffer(res.data)

                    const str = arrayBuffer2String(arrayBuffer);
                    value = str;
                } else {
                    value = res.data;
                }

            }
            if (receiveType === "2") { // HEX (16)
                console.log('HEX(16) '+receiveType, res);
                const str16 = buffer_to_hex(res.data);
                value = str16;
            }
            listMessage.push({
                ...formData,
                "value": value,
                "position": "left"
            })
            setList([...listMessage])


        };

        socketClient.onerror = (e) => {

        }
        socketClient.onclose = (e) => {

        };

        // return
        //
        // socketClient = io("http://localhost:3000");
        //
        // socketClient.on("connect", () => {
        //     console.log(socketClient.id, '-connect');
        //     setLinksSwitch(true)
        //
        //     const time = getDateTime();
        //     setStartTime(time)
        // })
        //
        // // 
        // socketClient.emit('open_tcp', {ip: linkDetail.ipAddress, port: linkDetail.portNumber},(e)=>{
        //     console.log(e)
        // });
    }
    const closeLinks = () => {

        socketClient.send(JSON.stringify({
            event: 'close_tcp',
            data: {}
        }))


        setLinksSwitch(false);

        const time = getDateTime();
        setEndTime(time)
    }
    const sendout = (values: any) => {
        if (!linksSwitch) {
            message.warning("")
            return
        }
        if (!formData.sendingType) {
            message.warning("")
            return
        }

        listMessage.push({
            "value": values.sendoutContent,
            ...formData,
            "position": "right"
        })
        setList([...listMessage])
        socketClient.send(JSON.stringify({
            event: 'send_tcp', data: {
                "value": values.sendoutContent,
                ...formData,
                "position": "left"
            }
        }));


        //   
        // socketClient.emit('send_tcp', values.sendoutContent, (data: any) => {
        //     console.log("", data)
        //
        //     listMessage.push({
        //         "value": data,
        //         ...formData,
        //         "position": "left"
        //     })
        //     setList([...listMessage])
        //
        // });

    }


    return (<>

            <div className={styles['r-banner']}>
                <div className={styles['rbr-jsq']}>
                    <div className={styles['rbrj-top']}>
                        <div className={styles['rbrj-left']}>
                             {formData.receiveType}
                            <Select
                                style={{width: 120, marginLeft: "10px"}}
                                onChange={(e) => {
                                    console.log(e)
                                    receiveType = e;
                                    setFormData({
                                        ...formData,
                                        "receiveType": e
                                    })
                                }}
                                value={formData.receiveType}
                                options={[
                                    {
                                        value: '1',
                                        label: 'ASCII',
                                    },
                                    {
                                        value: '2',
                                        label: 'HEX',
                                    },
                                ]}
                            />
                            <Button onClick={() => {
                                setList([])
                            }}></Button>
                        </div>
                        <div className={styles['rbrj-right']}>
                            <div></div>
                            <div></div>
                            <div></div>
                        </div>
                    </div>

                    <div className={styles['rbrj-body']}>
                        {
                            linksSwitch || list.length > 0 ?
                                <div className={styles['rbrj-body-title']}>
                                    {startTime} 
                                </div> : ''
                        }

                        {
                            list.map((item, index) => {
                                return <div key={index} className={styles['rbrj-body-message']}>
                                    <div
                                        className={styles[item.position === 'left' ? "left-message-box" : 'right-message-box']}
                                    >
                                        <div style={{marginBottom: "10px"}}>
                                            <div>{item.position === 'left' ? `TCP ${linkDetail.ipAddress}` : ' 14:44:42'}</div>
                                        </div>
                                        <div>{item.value}</div>
                                    </div>
                                </div>
                            })
                        }


                        {
                            linksSwitch === false && list.length > 0 ?
                                <div className={styles['rbrj-body-title']}>
                                    {endTime} 
                                </div> : ''
                        }

                    </div>
                </div>
                <div className={styles['rbr-fsq']}>
                    <div className={styles['rbrf-top']}>
                        
                        <Select
                            style={{width: 120, margin: "10px"}}
                            onChange={(e) => {
                                console.log(e)

                                setFormData({
                                    ...formData,
                                    "sendingType": e
                                })
                            }}
                            value={formData.sendingType}
                            options={[
                                {
                                    value: '1',
                                    label: 'ASCII',
                                },
                                {
                                    value: '2',
                                    label: 'HEX',
                                },
                            ]}
                        />
                        {/*<Checkbox onChange={() => {*/}
                        {/*}}></Checkbox>*/}
                        {/*<Checkbox onChange={() => {*/}
                        {/*}}></Checkbox>*/}

                        {/*<span style={{color: "#1890ff", margin: "0 20px"}}></span>*/}

                        <Checkbox checked={formData.cycleState} onChange={(e) => {
                            setFormData({
                                ...formData,
                                "cycleState": e.target.checked
                            })
                        }}></Checkbox>

                        <InputNumber
                            value={formData.timeMS}
                            onChange={(val) => {
                                setFormData({
                                    ...formData,
                                    timeMS: val.target ? val.target.value : val
                                });
                            }} placeholder="" addonAfter="ms" min={1}
                            style={{width: 120, marginLeft: "20px"}}/>


                    </div>
                    <div className={styles['rbrf-body']}>


                        <Form
                            layout={'horizontal'}
                            form={form}
                            onFinish={sendout}
                        >
                            <Form.Item label="" name="sendoutContent">
                                <TextArea rows={3} placeholder="" bordered={false}/>
                            </Form.Item>


                            <Form.Item>
                                <Button type="primary" htmlType="submit"></Button>

                            </Form.Item>
                        </Form>

                    </div>
                </div>
            </div>

        </>
    );
};

// export default connect(({common, select}) => ({
//     common,
//     select
// }))(TcpType);
export default withRouter(TcpType);
