import React, {useEffect, useState, useRef} from "react";
import {connect} from "dva";
import {PaginatedParams} from "ahooks/lib/useAntdTable/";
import {useAntdTable} from "ahooks";
import mqtt from 'mqtt/dist/mqtt'

import {
    Table,
    Button,
    Drawer,
    Radio,
    Form,
    Tag,
    Input,
    InputNumber,
    Tree,
    Checkbox,
    Col,
    Row,
    Select,
    Badge, Modal
} from "antd";

const {TextArea} = Input;
const {Search} = Input;
const {Option} = Select;
const {Column} = Table;

import columns from './columns';
import TABLEJSON from './TABLEJSON.tsx';
// import Test from './test.tsx';
import MqttType from './mqttType.tsx';
import WsType from './wsType.tsx';
import TcpType from './tcpType.tsx';

import styles from "./style.module.less";
import {
    PlusCircleOutlined,
    CaretRightOutlined,
    PoweroffOutlined,
    EditOutlined,
    DeleteOutlined
} from "@ant-design/icons";

const DebugLinks = (props: any) => {
    const {dispatch, select} = props;
    const [form] = Form.useForm();

    let ChildRef = React.createRef();
    const [linksSwitch, setLinksSwitch] = useState<boolean>(false);

    const [listLinks, setListLinks] = useState([]);
    const [linkDetail, setLinkDetail] = useState({});


    const [linksType, setLinksType] = useState<number>(1);
    const [formTitle, setFormTitle] = useState<string>('');//
    const [formIsModalVisible, setFormlIsModalVisible] = useState(false);//
    const [formData, setFormData] = useState<any>({});//

    useEffect(() => {
        getListLinks()
    }, []);

    // 
    const getListLinks = () => {
        dispatch({
            type: 'common/getRequestData',
            method: 'GET',
            url: "/data-acquisition/links-info",
            payload: {},
            callback: (res: any) => {
                if (res.success) {
                    let list = res.data;
                    setListLinks(list)

                    // 
                    if (list.length > 0) {
                        if (formData.id) {
                            const rows = list.filter(item => {
                                if (item.id === linkDetail.id) {
                                    return item
                                }
                            });
                            setLinkDetail({...rows[0]})
                        } else {
                            setLinkDetail({...list[0]})
                        }

                    }
                }
            },
        });
    }


    const onoffLinks = () => {
        const v = ChildRef.current.getLinkStatus()
        console.log(v)
        setLinksSwitch(v)
    }
    const addLinks = () => {
        setFormData({})
        setFormTitle("");
        setFormlIsModalVisible(true);
    }

    const detailLinks = (item: any) => {
        if (linksSwitch) {
            setLinksSwitch(false)
        }
        setLinkDetail(item)
    }

    const editLinks = (id: any) => {
        setLinksSwitch(false)

        if (!id) {
            return
        }
        dispatch({
            type: 'common/getRequestData',
            method: 'get',
            url: "/data-acquisition/links-info/" + id,
            payload: {},
            callback: (res: any) => {
                if (res.success) {
                    setLinksType(res.data.linksType)
                    setFormData(res.data)
                    setFormTitle("");
                    setFormlIsModalVisible(true);
                }
            },
        });
    }
    const deleteLinks = (id: any) => {
        if (!id) {
            return
        }
        Modal.confirm({
            title: '',
            icon: '',
            content: '',
            onOk() {
                dispatch({
                    type: 'common/getRequestData',
                    method: 'delete',
                    url: "/data-acquisition/links-info/" + id,
                    payload: {},
                    callback: (res: any) => {
                        if (res.success) {
                            getListLinks()
                        }
                    },
                });

            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }

    const addOrUpdate = () => {
        let _url = ""
        let _data = {}

        if (formTitle.includes("")) {
            if (formData.id) {
                _url = '/data-acquisition/links-info/update'
                _data = {...formData, "linksType": linksType}
            } else {
                _url = '/data-acquisition/links-info/create'
                _data = {...formData, "linksType": linksType}
            }
        }

        if (formTitle.includes("")) {
            if (formData.id) {
                _url = '/data-acquisition/subscribe-info/update'
                _data = {...formData, linksId: linkDetail.id}
            } else {
                _url = '/data-acquisition/subscribe-info/create'
                _data = {...formData, linksId: linkDetail.id}
            }
        }


        dispatch({
            type: 'common/getRequestData',
            method: 'post',
            url: _url,
            payload: {
                ..._data,
            },
            callback: (res: any) => {
                console.log(res)
                if (res.success) {
                    setFormlIsModalVisible(false);
                    if (formTitle.includes("")) {
                        getListLinks()


                    }
                    if (formTitle.includes("")) {

                    }
                }
            },
        });
    }


    const getTagColor = (item) => {
        if (item.typeCode === 1) {
            return "geekblue";
        }
        if (item.typeCode === 2) {
            return "orange";
        }
        if (item.typeCode === 3) {
            return "green";
        }
    }

    return (<div className={styles['debugLinks']}>
        <div className={styles['left']}>
            <Button onClick={() => {
                addLinks()
            }} type="primary" icon={<PlusCircleOutlined/>}></Button>
            <div className={styles['links']}>


                {
                    listLinks.length > 0 ? listLinks.map((item, index) => {
                        return <div key={index} onClick={() => {
                            detailLinks(item);
                        }} style={{background: item.id == linkDetail.id ? 'rgba(240, 244, 246, 1)' : ''}}
                                    className={styles['item']}>
                            <Tag color={getTagColor(item.linksType)}>{item.linksType?.typeName}</Tag>
                            {item.name ? item.name + '@' : ''}{item.mqttType}{item.ipAddress}:{item.portNumber}
                        </div>
                    }) : ''
                }
            </div>
        </div>
        <div className={styles['right']}>
            <div className={styles['r-top']}>
                <div className={styles['rt-left']}>
                    <div className={styles['rtl-title']}>{linkDetail.linksType?.typeName} </div>
                    <div className={styles['rtl-run']}><Badge status={linksSwitch ? 'processing' : 'default'} /></div>
                    <div className={styles['rtl-url']}>
                        {linkDetail.name ? linkDetail.name + '@' : ''}{linkDetail.mqttType}{linkDetail.ipAddress}:{linkDetail.portNumber}{linkDetail.linksPath}
                    </div>
                </div>
                <div className={styles['rt-right']}>
                    <Button onClick={() => {onoffLinks()}} icon={linksSwitch === false ? <PoweroffOutlined/> : <CaretRightOutlined/>}
                            type={linksSwitch == false ? 'primary' : 'danger'} ghost>
                        {linksSwitch == false ? '' : ''}
                    </Button>
                    <Button onClick={() => {
                        editLinks(linkDetail.id)
                    }} type="primary" icon={<EditOutlined/>}></Button>
                    <Button onClick={() => {
                        deleteLinks(linkDetail.id)
                    }} type="primary" icon={<DeleteOutlined/>} danger></Button>
                </div>
            </div>
            {/*<Test />*/}

            {
                linkDetail?.linksType?.typeCode === 1 ?
                    <MqttType onRef={ChildRef} linkDetail={linkDetail} select={select} dispatch={dispatch}/> : ''
            }
            {
                linkDetail?.linksType?.typeCode === 2 ?
                    <WsType onRef={ChildRef} linkDetail={linkDetail} select={select} dispatch={dispatch}/> : ''
            }
            {
                linkDetail?.linksType?.typeCode === 3 ?
                    <TcpType onRef={ChildRef} linkDetail={linkDetail} select={select} dispatch={dispatch}/> : ''
            }
        </div>


        {/* */}

        <Drawer title={formTitle} placement="right" onClose={() => {
            setFormlIsModalVisible(false)
        }} visible={formIsModalVisible} width={'50vw'}>

            <div className={styles['links-info']}>
                <div className={styles['ljfs']}>
                    <div className={styles['ljfs-title']}></div>
                    <Radio.Group defaultValue={linksType} onChange={(e) => {
                        setLinksType(e.target.value)
                    }} buttonStyle="solid">
                        {
                            select.links_type.map(item => {
                                return <Radio.Button value={item.typeCode}> {item.typeName}</Radio.Button>
                            })
                        }
                    </Radio.Group>
                </div>

                {
                    linksType === 1 ?
                        <>
                            <div className={styles['jccs']}>
                                <div className={styles['jccs-title']}> </div>
                                <div className={styles['m-box']}>
                                    <div className={styles['mb-row2']}>
                                        <div className={styles['mbr-left']}>
                                            <Form.Item required={true} label={``}>
                                                <Input value={formData.name}
                                                       onChange={(val) => {
                                                           setFormData({
                                                               ...formData,
                                                               name: val.target ? val.target.value : val
                                                           });
                                                       }}
                                                       placeholder=""/>
                                            </Form.Item>
                                        </div>
                                        <div className={styles['mbr-right']}>
                                            <Form.Item required={true} label={`Client ID`}>
                                                <Input value={formData.clientId}
                                                       onChange={(val) => {
                                                           setFormData({
                                                               ...formData,
                                                               clientId: val.target ? val.target.value : val
                                                           });
                                                       }}
                                                       placeholder="Client ID"/>
                                            </Form.Item>
                                        </div>
                                    </div>
                                    <div className={styles['mb-row2']}>
                                        <div className={styles['mbr-left']}>
                                            <Form.Item required={true} label={`IP`}>
                                                {/*<Input value={formData.typeName}*/}
                                                {/*       onChange={(val) => {*/}
                                                {/*           setFormData({*/}
                                                {/*               ...formData,*/}
                                                {/*               typeName: val.target ? val.target.value : val*/}
                                                {/*           });*/}
                                                {/*       }}*/}
                                                {/*       placeholder="IP"/>*/}


                                                <Input.Group compact>
                                                    <Select
                                                        value={formData?.mqttType}
                                                        onChange={(val: any, option: any) => {
                                                            setFormData({
                                                                ...formData,
                                                                mqttType: val.target ? val.target.value : val,
                                                            });
                                                        }} placeholder="" style={{width: '25%'}}>
                                                        {/*{*/}
                                                        {/*    select?.protocol_type.map((item) => {*/}
                                                        {/*        return <Option key={item.typeCode}*/}
                                                        {/*                       value={item.typeCode}>{item.typeName}</Option>*/}
                                                        {/*    })*/}
                                                        {/*}*/}
                                                        <Option value="mqtt://">mqtt://</Option>
                                                        <Option value="mqtts://">mqtts://</Option>
                                                        <Option value="ws://">ws://</Option>
                                                        <Option value="wss://">wss://</Option>
                                                    </Select>
                                                    <Input value={formData.ipAddress}
                                                           onChange={(val) => {
                                                               setFormData({
                                                                   ...formData,
                                                                   ipAddress: val.target ? val.target.value : val
                                                               });
                                                           }}
                                                           placeholder="IP" style={{width: '75%'}}/>
                                                </Input.Group>

                                            </Form.Item>
                                        </div>
                                        <div className={styles['mbr-right']}>
                                            <Form.Item required={true} label={``}>
                                                <Input value={formData.portNumber}
                                                       onChange={(val) => {
                                                           setFormData({
                                                               ...formData,
                                                               portNumber: val.target ? val.target.value : val
                                                           });
                                                       }}
                                                       placeholder=""/>
                                            </Form.Item>
                                        </div>
                                    </div>


                                    {
                                        formData?.mqttType === 'ws://' || formData?.mqttType === 'wss://' ?
                                            <div className={styles['mb-row2']}>
                                                <div className={styles['mbr-left']}>
                                                    <Form.Item required={true} label={`Path`}>
                                                        <Input value={formData.linksPath}
                                                               onChange={(val) => {
                                                                   setFormData({
                                                                       ...formData,
                                                                       linksPath: val.target ? val.target.value : val
                                                                   });
                                                               }}
                                                               placeholder=""/>
                                                    </Form.Item>
                                                </div>
                                                <div className={styles['mbr-right']}>
                                                </div>
                                            </div> : ''
                                    }

                                    <div className={styles['mb-row2']}>
                                        <div className={styles['mbr-left']}>
                                            <Form.Item required={false} label={``}>
                                                <Input value={formData.username}
                                                       onChange={(val) => {
                                                           setFormData({
                                                               ...formData,
                                                               username: val.target ? val.target.value : val
                                                           });
                                                       }}
                                                       placeholder=""/>
                                            </Form.Item>
                                        </div>
                                        <div className={styles['mbr-right']}>
                                            <Form.Item required={false} label={``}>
                                                <Input.Password value={formData.password}
                                                                onChange={(val) => {
                                                                    setFormData({
                                                                        ...formData,
                                                                        password: val.target ? val.target.value : val
                                                                    });
                                                                }}
                                                                placeholder=""/>
                                            </Form.Item>
                                        </div>
                                    </div>

                                </div>
                            </div>

                            <div className={styles['gjcs']}>
                                <div className={styles['gjcs-title']}></div>
                                <div className={styles['m-box']}>
                                    <div className={styles['mb-row2']}>
                                        <div className={styles['mbr-left']}>
                                            <Form.Item required={true} label={``}>
                                                <InputNumber value={formData.linksDuration}
                                                             onChange={(val) => {
                                                                 setFormData({
                                                                     ...formData,
                                                                     linksDuration: val.target ? val.target.value : val
                                                                 });
                                                             }} placeholder="" addonAfter="s"
                                                             style={{width: '100%'}}/>


                                            </Form.Item>
                                        </div>
                                        <div className={styles['mbr-right']}>
                                            <Form.Item required={true} label={`Keep Alive`}>

                                                <InputNumber value={formData.keepAlive}
                                                             onChange={(val) => {
                                                                 setFormData({
                                                                     ...formData,
                                                                     keepAlive: val.target ? val.target.value : val
                                                                 });
                                                             }} placeholder="Keep Alive" addonAfter="s"
                                                             style={{width: '100%'}}/>


                                            </Form.Item>
                                        </div>
                                    </div>
                                    <div className={styles['mb-row2']}>
                                        <div className={styles['mbr-left']}>
                                            <Form.Item required={true} label={``}>
                                                <Radio.Group value={formData.clearSession}
                                                             onChange={(val) => {
                                                                 setFormData({
                                                                     ...formData,
                                                                     clearSession: val.target ? val.target.value : val
                                                                 });
                                                             }}>
                                                    <Radio value="true">true</Radio>
                                                    <Radio value="false">false</Radio>
                                                </Radio.Group>
                                            </Form.Item>
                                        </div>
                                        <div className={styles['mbr-right']}>

                                        </div>
                                    </div>

                                </div>
                            </div>
                        </> : ''
                }

                {
                    linksType === 2 ?
                        <>
                            <div className={styles['jccs']}>
                                <div className={styles['jccs-title']}> </div>
                                {/*<div className={styles['m-box']}>*/}
                                {/*    <div className={styles['mb-row2']}>*/}
                                {/*        <div className={styles['mbr-left']}>*/}
                                <Form.Item required={true} label={`IP`}>
                                    <Input value={formData.ipAddress}
                                           onChange={(val) => {
                                               setFormData({
                                                   ...formData,
                                                   ipAddress: val.target ? val.target.value : val
                                               });
                                           }}
                                           placeholder="IP"/>
                                </Form.Item>
                                {/*</div>*/}
                                {/*<div className={styles['mbr-right']}>*/}
                                <Form.Item required={true} label={``}>
                                    <Input value={formData.portNumber}
                                           onChange={(val) => {
                                               setFormData({
                                                   ...formData,
                                                   portNumber: val.target ? val.target.value : val
                                               });
                                           }}
                                           placeholder=""/>
                                </Form.Item>
                                {/*    </div>*/}
                                {/*</div>*/}


                                <Form.Item required={true} label={`Path`}>
                                    <Input
                                        value={formData.linksPath ? formData.linksPath : "?sid=0126e70026a74d29b12a584a5bfd3e3f1001"}
                                        onChange={(val) => {
                                            setFormData({
                                                ...formData,
                                                linksPath: val.target ? val.target.value : val
                                            });
                                        }}
                                        placeholder=""/>
                                </Form.Item>

                                {/*</div>*/}
                            </div>

                        </> : ''
                }

                {
                    linksType === 3 ?
                        <>
                            <div className={styles['jccs']}>
                                <div className={styles['jccs-title']}> </div>
                                {/*<div className={styles['m-box']}>*/}
                                {/*    <div className={styles['mb-row2']}>*/}
                                {/*        <div className={styles['mbr-left']}>*/}
                                <Form.Item required={true} label={`IP`}>
                                    <Input value={formData.ipAddress}
                                           onChange={(val) => {
                                               setFormData({
                                                   ...formData,
                                                   ipAddress: val.target ? val.target.value : val
                                               });
                                           }}
                                           placeholder="IP"/>
                                </Form.Item>
                                {/*</div>*/}
                                {/*<div className={styles['mbr-right']}>*/}
                                <Form.Item required={true} label={``}>
                                    <Input value={formData.portNumber}
                                           onChange={(val) => {
                                               setFormData({
                                                   ...formData,
                                                   portNumber: val.target ? val.target.value : val
                                               });
                                           }}
                                           placeholder=""/>
                                </Form.Item>
                                {/*    </div>*/}
                                {/*</div>*/}


                                {/*</div>*/}
                            </div>

                        </> : ''
                }

                <div className={styles['btn']}>
                    <Button onClick={() => {
                        addOrUpdate()
                    }} type="primary"> </Button>
                    <Button onClick={() => {
                        setFormlIsModalVisible(false)
                    }}></Button>
                </div>
            </div>
        </Drawer>
    </div>);
};

export default connect(({common, select}) => ({
    common,
    select
}))(DebugLinks);
